* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, p {
  margin: 0;
}

.search-form {
  background-color: #06654b;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 20px;
  display: flex;
}

.search-form__input {
  height: 45px;
  border: none;
  border-radius: 5px;
  padding: 10px;
  font-size: 20px;
}

.search-form__button {
  fill: #212121;
  cursor: pointer;
  background-color: #dbdbdb;
  border: none;
  border-radius: 5px;
  padding: 10px;
  font-size: 25px;
  display: flex;
}

.gallery {
  flex-flow: wrap;
  gap: 15px;
  padding-left: 15px;
  padding-right: 15px;
  display: flex;
}

.photo-card {
  flex-basis: calc(25% - 20px);
}

.info {
  border: 1px solid #eee;
  border-top: none;
  gap: 10px;
  padding: 10px 15px;
  display: flex;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.info-item {
  text-align: center;
  flex-direction: column;
  flex-basis: calc(25% - 10px);
  font-size: 14px;
  display: flex;
}

.info-item b {
  font-size: 16px;
}

.load-more {
  margin: 25px auto;
  display: block;
}

.load-more.is-hidden {
  display: none;
}

